@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@500;600&family=Open+Sans:ital,wght@0,500;1,400&family=Poppins:ital@0;1&family=Roboto&family=Rubik+Pixels&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.form{
    width:27rem !important
}
Button{
    background-color: #ffd831 !important;
    color: black !important;
    border: none !important
    
}
Button:hover{
    background-color: #857114 !important;
    color: rgb(242, 235, 235) !important;

}
.head {
    background-color: #1c212d;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }
  
.subnav {
    width: 18%;
    align-items: center;
    /* align vertically center */
    justify-content: center;
    /* align horizontally center */

  

}
.subnav h1{
    padding: 3rem 0px 0px 2rem;
    color: white;
    cursor: pointer;
}
.subnav h1 .head-icon{
    color: #ffd831;
    font-size: 2rem;
}

.head {
    background-color: #1c212d;
    height: 730px;

}

.subhome {
    background-color: aliceblue;
    height: 712px;

    margin: 1rem 3px 3px 3px;
    width: 82%;
    border-top-left-radius: 1%;
    border-top-right-radius: 1%;
    border: 1px solid rgb(125, 119, 119);
    padding-top: 5px;
    overflow-x: auto;
}

.nav {
    flex-direction: column;
    padding: 4px 4px 2px 10px ;
    margin-top: 12rem;
}
.nav p{
    padding: 6px 0px 6px 3rem ;
  
    color: white;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
}
.nav p:hover {
    background-color: #ffd831;
    border-radius: 5px;
    color: rgb(11, 4, 31);
  }
  
  .nav p:hover .icon {
    color: rgb(11, 4, 31);
  }
  
p .icon {
  
    margin-right: 0.45rem;
    font-size: 1rem;
    padding: 5px 0px 0px 0px;
    
}
.link {
    text-decoration: none;
  }
  


.main{
    margin-top: 3rem;
   
}
.icon{
    font-size: 1.6rem;
    color: white;
    padding: 5px;
    border-radius: 5px;
}
.title{
    margin-top: 1rem;
    color: gray;
}
.subtitle{
    font-weight: bolder;
    font-size: 1.7rem;
}

::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; }

html {
  scroll-behavior: smooth;
}
button{
    border: none;
    padding: 5px;
}
